@import url(https://fonts.googleapis.com/css?family=Fjalla+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 70vw;
  height: 90vh;
  margin-top: 10vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@media screen and (max-width: 480px) {
  h1 {
    font-size: 2em !important;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes animat {
  from {
    opacity: 0;
  }
  to {
    opacity: 4;
  }
}

.todo {
  font-size: 25px;
  font-style: italic;
  display: flex;
  margin-top: 0.5em;
  width: 70vw;
  animation-name: animat;
  animation-duration: 1s;
  animation-iteration-count: 1;
}

@keyframes opacity {
  from {
    opacity: 4;
  }
  to {
    opacity: 0;
  }
}
.opacity {
  animation-name: opacity;
  animation-duration: 1s;
  animation-iteration-count: 1;
}

.done {
  width: 100%;
  margin-right: 10px;
  text-decoration: line-through;
  border: 1px solid black;
  background-color: #ffc8ff;
  padding: 0 10px;
  opacity: 0.5;
  border-radius: 10px;
}

.notDone {
  border-radius: 10px;
  width: 100%;
  margin-right: 10px;
  border: black 1px solid;
  background-color: #ffc8ff;
}

.notDone:hover {
  background-color: #93ffc1;
}
.done:hover {
  background-color: #93ffc1;
}

.buttond {
  background-image: url(https://cdn.dribbble.com/users/2124240/screenshots/6118828/delete_icon_intraction.gif);
  background-size: cover;
  border-radius: 10px;
}
.buttond:hover {
  background-color: black !important;
  color: white;
}

.todos-container {
  height: auto;
  overflow: auto;
  margin-bottom: 5vh;
  padding: 10px;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/38816/image-from-rawpixel-id-2210775-jpeg.jpg")
    center center no-repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  display: grid;
  align-items: center;
  justify-items: center;
}
form {
  width: 70vw;
  display: flex;
  justify-content: space-around;
}

.contact-us {
  background: #f8f4e5;
  padding: 50px 100px;
  border: 2px solid rgba(0, 0, 0, 1);
  box-shadow: 15px 15px 1px #ffa580, 15px 15px 1px 2px rgba(0, 0, 0, 1);
}

input {
  display: block;
  height: 32px !important;
  width: 100%;
  font-size: 1em;
  line-height: 1em;
  font-family: 1em;
  margin-bottom: 1em;
  margin-right: 2em;
  border: none;
  border-bottom: 3px solid rgba(0, 0, 0, 1);
  background: #f8f4e5;
  min-width: 250px;
  padding-left: 5px;
  outline: none;
  color: rgba(0, 0, 0, 1);
  border-radius:10px;
}

input:focus {
  border-bottom: 3px solid #ffa580;
}

button {
  display: block;
  height: 32px !important;
  margin: 0 auto;
  padding: 0 20px;
  background: #ffa580;
  letter-spacing: 2px;
  transition: 0.2s all ease-in-out;
  outline: none;
  border-radius:30px;
  border: 1px solid rgba(0, 0, 0, 1);
  box-shadow: 3px 3px 1px #95a4ff, 3px 3px 1px 1px rgba(0, 0, 0, 1);
  
}

button:hover {
  background: rgba(0, 0, 0, 1);
  color: white;
  border: 1px solid rgba(0, 0, 0, 1);
}

::selection {
  background: #ffc8ff;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  border-bottom: 5px solid #95a4ff;
  -webkit-text-fill-color: #2a293e;
  -webkit-box-shadow: 0 0 0px 1000px #f8f4e5 inset;
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

/* Hermann's additions*/

h1 {
  font-size: 4em;
  margin-bottom:1em;
}

